html, body {
  width: 100%;
  height: 100%;
  margin: 0;
}
/* menu styles */
.nav {
  min-height: 10vh;
  color: #333;
  font-size: 12px;
  font-family: "jetbrains_monoregular";
  text-align: right;
}
.nav-links {
  text-decoration: 'none';
}
.nav-links, ul {
  display: flex;
  list-style: none;
  justify-content: flex-end;
}
.nav-links ul, li {
  padding: 10px 20px;
  margin-top: 20px;
}
.nav-links ul, li:hover {
  color: #FF0081;
  transition: all .25s cubic-bezier(.645,.045,.355,1) 0s;
}
label {
  margin: 0 40px 0 0;
  font-size: 26px;
  line-height: 10vh;
  width: 30px;
  height: 30px;
  padding: 10px;
  display: none;
}
#toggle {
  display: none;
}
@media only screen and (max-width: 768px) {
 label {
  display: block;
  cursor: pointer;
 }
 .nav-links {
  width: 50%;
  background-color: rgb(201, 201, 201);
  text-align: center;
  display: none;
 }
 .nav-links, ul, li {
   flex-direction: column;
   border-bottom: 1px solid #fff;
   margin: 0;  
 }
 #toggle:checked + .nav-links {
   display: block;
   margin: 0;
 }
}
/* tipography */
@font-face {
  font-family: 'jetbrains_monoregular';
  src: url('./fonts/jetbrainsmono-regular-webfont.woff2') format('woff2'),
       url('./fonts/jetbrainsmono-regular-webfont.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}
@import url('https://fonts.googleapis.com/css?family=Roboto&display=swap');

/* main div styles */
.firstHomeDiv {
  display: block;
  position: relative;
  width: 100%;
  padding: 10px;
  
}
.firstHomeDivContent {
  width: 50%;
  position: absolute;
  margin-top: 5%;
  left: 20%;
}
/* title / subtitle */ 
.customP {
  color: #FF0081;
  font-family: jetbrains_monoregular;
  font-size: 14px;
}

h1 {
  font-size: 40px;
  margin-top: 0.1em;
  margin-bottom: 0.1em;
  line-height: 1;
}

h2 {
  font-size: 40px;
  color: #FF77BC;
  margin-top: 0.1em;
  margin-bottom: 0.1em;
  line-height: 1;
}
h3 {
  margin-top: 0.1em;
  line-height: 1;
}
h3:hover {
  color: #FF0081;
  transition: all .25s cubic-bezier(.645,.045,.355,1) 0s;
}
p {
  font-family: Roboto;
}
a {
  text-decoration: none;
}
/* button style */ 
.btn {
  border: 1px solid #FF0081;
  padding: 1.5%;
  color: #FF0081;
  background-color: transparent;
  font-family: Roboto;
  text-align: center;
}
/* Tab Name from Experience Page */
.MuiTab-wrapper {
  text-transform: capitalize;
  font-family: jetbrains_monoregular;
}
.MuiTab-wrapper:hover {
  color: #FF0081;
  text-transform: capitalize;
  font-family: jetbrains_monoregular;
  transition: all .25s cubic-bezier(.645,.045,.355,1) 0s;
}
/* When select text */
::-moz-selection { /* Code for Firefox */
  color: white;
  background: #FF0081;
}
::selection {
  color: white;
  background: #FF0081;
}

.verticalText {
writing-mode: vertical-rl;
text-orientation: sideways-right;

}
.dividerDiv {
  position: relative;
  height: 10em;
}
.divider {
  margin-top: 20px;
}
/* Works page */
.firstProject {
  text-align: right;
  margin-top: 40px;
}
.projectDescription {
  background-color: gray;
  color: #fff;
  padding: 5%;
}
.projectTechnologies {
  font-family: jetbrains_monoregular;
  font-size: 11px;
}
.fingerFoodImg {
  margin-top: 20px;
  width: 100%;
}
.projectsP {
  color: #FF0081;
  font-family: jetbrains_monoregular;
  font-size: 11px;
}
.emptyDiv {
  margin-top: 50px;
}
.secondProject {
  margin-top: 50px;
}
.caesarCipher {
  width: 100%;
}
/* work page finish */

.homeImg {
  margin-top: 15%;
  margin-left: 9%;
  width: 25%;
}